<template lang="pug">
    div.content
        div.others-user-home
            mt-loadmore(
                :top-method="loadTop"
                ref="loadmore"
                @top-status-change="handleTopChange"
            )
                mint-loadmore-top(:topStatus="topStatus" slot="top")
                div.others-home-content
                    div.user-index-top
                        div.mask
                            img(:src="user.head_image || ''", alt="")
                        div.user-index-top-inner
                            div.avator
                                img.avator-img(:src="user.head_image || ''", alt="")
                                img.icon-vip(:src="user.v_icon", alt="" v-if="user.is_authentication==2")
                            div.nick-name
                                span(v-text="user.nick_name || ''")
                                img.icon-sex(:src="iconSex" alt="性别")
                                img.icon-level(:src="iconLevel" alt="用户等级")
                            div.signature
                                span(v-text="user.signature" v-if="user.signature")
                                span(v-else) Ta好像忘记签名了
                            div.ID(v-text="`账号：${user.user_id || ''}`")
                            div.authentication(v-if="user.is_authentication == 2")
                                i.icon.iconfont &#xe603;
                                span(v-text="`认证:${user.v_explain}`")
                    div.user-index-total
                        div.total-item
                            a(href="javascript:")
                                label 送出
                                span(v-text="user.use_diamonds")
                        div.total-item
                            router-link(:to="{ path: '/user/tool/follow', query: { to_user_id: to_user_id }}")
                                label 关注
                                span(v-text="user.focus_count")
                        div.total-item
                            router-link(:to="{ path: '/user/tool/fans', query: { to_user_id: to_user_id }}")
                                label 粉丝
                                span(v-text="user.fans_count")
                    div.list-block.contribution-bar
                        ul
                            li
                                router-link.item-content.item-link(:to="{ path: '/user/dedicate', query: { to_user_id: to_user_id }}")
                                    div.item-media
                                        svg.svg-icon(aria-hidden="true")
                                            use(xlink:href="#icon-gongxianbang")
                                    div.item-inner
                                        div.item-title 秀票贡献榜
                                        div.item-after
                                            template(v-for="(item, index) in cuser_list")
                                                img.avator.one(
                                                    :src="item.head_image"
                                                    v-if="index == 0"
                                                )
                                                img.avator.two(
                                                    :src="item.head_image"
                                                    v-if="index == 1"
                                                )
                                                img.avator.three(
                                                    :src="item.head_image"
                                                    v-if="index == 2"
                                                )
                    ul.user-tab-bar(slot="pagination")
                    swiper.user-tab-content(:options="swiperOption" ref="swiperOthers" :style="{height: swiperContainerHeight+'px'}")
                        swiper-slide
                            div.live-container
                                div.list-block
                                    ul
                                        li.item-content(v-for="(item, index) in user.item")
                                            div.item-inner
                                                div.item-title(v-text="index")
                                                div.item-after(v-text="item")
                        swiper-slide
                            div.div.live-container
                                live-broadcast(
                                    v-on:init="init"
                                )
                        swiper-slide
                            div.div.live-container
                                small-video(
                                    style="margin-top:.5rem;"
                                    v-on:init="init"
                                )

        ul.bottom-operation(v-if="userInfo.user_id != user.id")
            li(@click="setFollow")
                template(v-if="has_focus")
                    i.icon.iconfont &#xe689;
                    span 取消关注
                template(v-else)
                    i.icon.iconfont &#xe681;
                    span 关注
            li(@click="setBlack")
                i.icon.iconfont &#xe65d;
                span(v-if="has_black") 解除拉黑
                span(v-else) 拉黑
</template>
<script>
import userHomeTop from '@/components/user/userHomeTop'
import mintLoadmoreTop from '@/components/mintLoadmoreTop'
import liveBroadcast from '@/views/user/tool/live_broadcast'
import smallVideo from '@/views/user/tool/small_video'
import { swiper, swiperSlide } from 'vue-awesome-swiper'
import 'vue-awesome-swiper/node_modules/swiper/dist/css/swiper.min.css'
import queryString from 'queryString'
import { mapGetters } from 'vuex'
export default {
    name: 'othersIndex',
    components: {
        userHomeTop,
        swiper,
        swiperSlide,
        mintLoadmoreTop,
        liveBroadcast,
        smallVideo
    },
    data() {
        return {
            to_user_id: '',
            allLoaded: false,
            topStatus: '',
            iconLevel: '',
            iconSex: '',
            user: '',
            cuser_list: '',
            has_black: '',
            has_focus: '',
            playback: '',
            swiperContainerHeight: '',
            listCategory: [
                {
                    title: '主页',
                    type: 'day'
                },
                {
                    title: '直播',
                    type: 'month'
                },
                {
                    title: '小视频',
                    type: 'all'
                },
            ],
            swiperOption: {
                initialSlide: 0,
                loop: false,
                pagination: '.user-tab-bar',
                paginationClickable: true,
                paginationBulletRender: (swiper, index, className) => {
                    return `<li class="${className}"><span>${this.listCategory[index].title}</span></li>`;
                }
            }
        }
    },
    created() {
        this.getData();
    },
    methods: {
        getData(isRefresh) {
            this.to_user_id = this.$route.query.to_user_id;
            let axiosUrl = process.env.NODE_ENV == 'development' ? '/gm/api/user_home' : this.api.getUserHome();
            this.axios.get(axiosUrl, {
                params: {
                    to_user_id: this.to_user_id
                }
            }).then(res => {
                if (res.data.status == 1) {
                    console.log(res.data.user);
                    this.user = res.data.user;
                    this.cuser_list = res.data.cuser_list;
                    this.has_black = res.data.has_black;
                    this.has_focus = res.data.has_focus;
                    this.iconLevel = require(`@/assets/img/rank/rank_${this.user.user_level}.png`);
                    this.iconSex = res.data.user.sex == 1 ? require(`@/assets/img/sex-man.png`) : require(`@/assets/img/sex-woman.png`);
                }
                else {
                    this.Toast(res.data.error || '请求出错');
                }
                this.$nextTick(() => {
                    isRefresh && this.$refs.loadmore.onTopLoaded();
                    this.init();
                });
            })
                .catch(err => {
                    this.Toast(err || '网络异常，请求失败');
                    isRefresh && this.$refs.loadmore.onTopLoaded();
                });
        },
        loadTop() {
            this.getData(true);
        },
        handleTopChange(status) {
            // 获取下拉最新状态
            this.topStatus = status;
        },
        setBlack() {
            // 拉黑
            this.axios.post(this.api.userSet_black(), queryString.stringify({
                to_user_id: this.to_user_id
            }))
                .then(res => {
                    if (res.data.status == 1) {
                        this.has_black = res.data.has_black;
                    }
                    else {
                        this.Toast(res.data.error || '操作失败');
                    }
                })
                .catch(err => {
                    this.Toast(err || '网络异常，请求失败');
                });
        },
        setFollow() {
            // 关注
            this.axios.post(this.api.userFollow(), queryString.stringify({
                to_user_id: this.to_user_id
            }))
                .then(res => {
                    if (res.data.status == 1) {
                        this.has_focus = res.data.has_focus;
                    }
                    else {
                        this.Toast(res.data.error || '操作失败');
                    }
                })
                .catch(err => {
                    this.Toast(err || '网络异常，请求失败');
                });
        },
        init() {
            let self = this,
                liveContainer_h = $(".swiper-slide-active").find(".live-container").height()+10;
            self.swiperContainerHeight = liveContainer_h > 160 ? liveContainer_h : 160;

            self.swiperOption.initialSlide = 1;
            self.swiperOption.onSlideChangeStart = (swiper) => {
                $(".page-content").scrollTop(0);
                self.realIndex = swiper.realIndex;

                let liveContainer_h = $(".swiper-slide-active").find(".live-container").height();
                self.swiperContainerHeight = liveContainer_h > 160 ? liveContainer_h : 160;
            };
        },
    },
    computed: {
        ...mapGetters({
            userInfo: 'getUserInfo'
        })
    }
}
</script>
<style lang="less" scoped>
@import '../../../assets/css/variable.less';
.others-user-home {
    padding-bottom: 2.5rem;
    margin-bottom: .5rem;
}

.contribution-bar {
    .item-content {
        padding-left: .5rem;
    }
    .item-inner {
        margin-left: .5rem;
    }
    .item-after {
        max-height: max-content;
    }
    .svg-icon {
        width: 1.2rem;
        height: 1.2rem;
    }
    .avator {
        width: 1.6rem;
        height: 1.6rem;
        border-radius: 1.6rem;
        border: 1px solid #fff;
        &.one,
        &.two {
            position: relative;
            margin-right: -.25rem;
        }
        &.one {
            z-index: 1;
        }
    }
}

.bottom-operation {
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 2.5rem;
    line-height: 2.5rem;
    display: flex;
    background-color: #fff;
    border-top: 1px solid @bdcolor;
    li {
        position: relative;
        width: 100%;
        text-align: center;
        font-size: .6rem;
        display: flex;
        justify-content: center;
        align-items: center;
        .iconfont {
            margin-right: .4rem;
            font-size: .75rem;
        }
        &:after {
            content: '';
            position: absolute;
            right: 0;
            top: .625rem;
            display: block;
            width: 1px;
            height: 1.25rem;
            background-color: #e6e8f1;
        }
        &:last-child {
            &:after {
                content: none;
            }
        }
    }
}

.user-index-total {
    position: relative;
    padding: .75rem 0;
    display: flex;
    background-color: #fff;
    margin: .5rem;
    border-radius: 3px;
    margin-top: -1rem;
    .total-item {
        width: 100%;
        text-align: center;
        font-size: .6rem;
        border-right: 1px solid @bdcolor;
        a {
            color: @fc-light;
            label {
                display: block;
                font-size: .6rem;
            }
            span {
                color: @fc-main;
            }
        }
        &:last-child {
            border-right: 0;
        }
    }
}
.user-index-top {
    position: relative;
    width: 100%;
    height: 12.5rem;
    .mask {
        width: 100%;
        height: 100%;
        background-color: rgba(0, 0, 0, .85);
        img {
            width: 100%;
            height: 100%;
            filter: blur(10px);
            opacity: .2;
        }
    }
    .user-index-top-inner {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: 999;
        color: #fff;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        padding: .5rem;
        .nick-name {
            margin-top: .25rem;
            display: flex;
            align-items: center;
            justify-content: center;
            width: 100%;
            padding: 0 .5rem;
            .edit{
                margin-left: .125rem;
            }
        }
        .signature,
        .ID,
        .authentication {
            font-size: .6rem;
            margin-top: .25rem;
            display: flex;
            align-items: center;
        }
        .icon-sex {
            margin: 0 .125rem;
        }
        .authentication{
            .iconfont{
                margin-right: .15rem;
                font-size: .75rem;
            }
        }
    }
    .avator {
        position: relative;
        width: 4.5rem;
        height: 4.5rem;
        .avator-img {
            width: 4.5rem;
            height: 4.5rem;
            border-radius: 50%;
            border: 2px solid #fff;
        }
        .icon-vip {
            position: absolute;
            bottom: .25rem;
            right: .25rem;
            width: .6rem;
            height: .6rem;
        }
    }
}
.user-tab-content {
    .list-block {
        .item-title {
            width: 3rem;
            flex-shrink: 0;
        }
        .item-after {
            white-space: initial;
            max-height: initial;
            flex-shrink: 1;
        }
    }
}
</style>




