<template lang="pug">
    ul(v-if="list && list.length")
        li.playback-list(v-for="item in list")
            router-link(:to="{ path: '/room/index', params: { id: item.room_id }}")
                div.list-info
                    div.title(v-text="item.title")
                    div.time(v-text="item.begin_time_format")
                div.video-count
                    i.icon.iconfont &#xe651;
                    span(v-text="item.max_watch_number")
        <div v-if="allLoaded" class="no-more-data">无更多数据</div>
    null-data(v-else)
</template>
<script>
import nullData from '@/components/nullData'
export default {
    props: ['list', 'allLoaded'],
    components: {
        nullData
    }
}
</script>

<style lang="less" scoped>
@import "../assets/css/variable.less";
.playback-list {
    height: 3rem;
    border-bottom: 1px solid #e5e5e5;
    background-color: #fff;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 .5rem;
    .list-info {
        color: @fc-light;
    }
    .video-count {
        display: flex;
        align-items: center;
        .iconfont {
            font-size: 1rem;
            color: @fc-main;
        }
        span {
            font-size: .6rem;
            color: @fc-main;
            margin-left: .25rem;
        }
    }
}
.nullData {
    margin-top: 8.25rem;
}
</style>



